    function readyClick(readyStatus) {
        setChangeReady(readyStatus);

        console.log('    : ' + ready_cnt + ' : ' + startMember);

        const restURL = BaseURL + '/waiting-room/user-ready   ';

        const reqHeaders = {
            headers: {
                authorization: 'Bearer ' + save_token,
            },
        };

        axios
            .patch(
                restURL,
                {
                    room_idx: parseInt(room_idx), //  .
                    user_ready: readyStatus,
                },
                reqHeaders
            )
            .then(function (response) {
                console.log(' rest: ' + readyStatus);
            })
            .catch(function (error) {
                alert('error Start' + error.message);
            });
    }

    function startClick() {
        const restURL = BaseURL + '/game/start';

        const reqHeaders = {
            headers: {
                authorization: 'Bearer ' + save_token,
            },
        };

        axios
            .post(
                restURL,
                {
                    room_idx: parseInt(room_idx),
                },
                reqHeaders
            )
            .then(function (response) {
                //response jwt token 
                console.log('success! ');
                //  ,   call   
                setGameStart(true);
            })
            .catch(function (error) {
                alert(error);
            });
    }

    function colorClick(str) {
        const restURL = BaseURL + '/waiting-room/user-color';

        const reqHeaders = {
            headers: {
                authorization: 'Bearer ' + save_token,
            },
        };

        axios
            .patch(
                restURL,
                {
                    room_idx: parseInt(room_idx), //   .
                    user_color: str, //   
                },
                reqHeaders
            )
            .then(function (response) {
                console.log(' rest: ' + response.data);
                setSelectColor(str); //  
            })
            .catch(function (error) {
                alert('error ' + error.message);
            });
    }

